<template>
	<div v-if="verificationRobotVisible" class="overlay" @click.stop>
		<div id="verification-robot-html"></div>
	</div>
    <!-- <div id="robot"></div> -->

</template>

<script>
	import { ref, nextTick } from 'vue'
export default {
  name: 'GoogleRecaptcha',
  props: ["sitekey"], // 所要传的秘钥
  data(){
	return {
		verificationRobotVisible:ref(false),
		challenge_response:ref("") // 令牌
	}
  },
  mounted() {
	
    // window.ReCaptchaLoaded = this.loaded;
    // var script = document.createElement("script");
    // script.src =
    //   "https://recaptcha.net/recaptcha/api.js?οnlοad=ReCaptchaLoaded&render=explicit";
    // document.head.appendChild(script);
  },
  created(){
	
  },
  methods: {
	verificationRobot:async (_this) =>{
		// console.log(_this.key)
		console.log(_this.$refs.recaptcha.verificationRobotVisible)
		console.log(_this.$refs.recaptcha.sitekey)
		// const verificationRobotVisible = ref(false);
		// console.log(sitekey)
		
		console.log(_this.$refs.recaptcha.challenge_response)
		// const verificationRobot = async () => {
		if (_this.$refs.recaptcha.challenge_response) return;
		try {
			_this.$refs.recaptcha.verificationRobotVisible = true
			await nextTick();
			console.log(window.grecaptcha)
			// render方法 将容器呈现为reCAPTCHA微件，并返回新创建的微件的ID
			const optWidgetId = window.grecaptcha?.render(
				"verification-robot-html",
				{
				// 您申请的网站密钥
				sitekey: _this.$refs.recaptcha.sitekey,
				// 当用户提交成功的响应时执行的回调，并返回令牌
				callback: (response) => {
					console.log(_this.$refs.recaptcha)
					console.log(response)
					_this.$refs.recaptcha.challenge_response = response;
					_this.$refs.recaptcha.verificationRobotVisible = false;
				},
				// 遇到错误，并且在恢复连接之前无法继续时执行的回调
				"error-callback": () => {
					_this.$refs.recaptcha.verificationRobotVisible = false;
					console.log("机器人验证失败，请稍后再试！");
				},
				// 响应过期且用户需要重新验证时执行的回调(就是拿到的令牌过期了)
				"expired-callback": () => {
					_this.$refs.recaptcha.challenge_response = "";
					_this.$refs.recaptcha.verificationRobotVisible = true;
					// reset方法 重置reCAPTCHA微件
					nextTick(() => window.grecaptcha?.enterprise?.reset(optWidgetId)); 
				}
			}
		);
		} catch (error) {
			_this.$refs.recaptcha.verificationRobotVisible = false;
			console.log("机器人验证失败，请稍后再试！");
		}
	},
  },
};
